.page-intro {
    position: relative;
    background: linear-gradient(
		45deg,
		darken( theme-color( 'primary' ), 15% ) 5%,
		theme-color( 'primary' ) 35%,
		lighten( theme-color( 'primary' ), 10% ) 100%
	);
	padding: 90px 0 40px;
    z-index: 1;
	
	@include media-breakpoint-up(sm) { 
		padding: 130px 0 80px;
	}

    &__headline {
		color: white;
		font-size: 3.5rem;
    }
}